.profile-banner{
  margin-bottom: 100px;
  padding-top: 20px;
  @include media(">=tablet") {
    padding-top: 57px;
  }
  @include media(">=desktop") {
    display: flex;
  }
  @include media(">=widescreen") {
    margin-bottom: 246px;
  }
  .text-holder{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    color: #000;
    margin-bottom: 20px;
    @include media(">=desktop") {
      max-width: 50%;
      padding-right: 10px;
      margin-bottom: 0;
    }
  }
  .profile-info{
    line-height: 1.1;
    @include media(">=desktop") {
      padding-left: 140px;
      position: relative;
    }
    .avatar{
      width: 120px;
      height: 120px;
      border-radius: 50%;
      overflow: hidden;
      margin: 0 auto 10px;
      @include media(">=desktop") {
        margin: 0;
        position: absolute;
        top: 0;
        left: 0;
      }
      img{
        width: 100%;
      }
    }
    h1{
      font-size: 36px;
      font-weight: 700;
      margin-bottom: 12px;
      text-align: center;
      @include media(">=tablet") {
        font-size: 42px;
      }
      @include media(">=desktop") {
          text-align: left;
      }
    }
    .text{
      font-size: 26px;
      font-weight: 600;
      margin-bottom: 20px;
      @include media(">=tablet") {
        font-size: 34px;
      }
      p{
        margin-bottom: 20px;
        &:last-child{
          margin-bottom: 0;
        }
      }
    }
  }
  .worked-block{
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    h5{
      font-size: 14px;
      font-weight: 400;
      margin: 0 10px 0 0;
    }
    .worked-list{
      list-style-type: none;
      margin: 0;
      padding: 0;
      display: flex;
      flex-wrap: wrap;
      li{
        width: 30px;
        height: 30px;
        border-radius: 50%;
        overflow: hidden;
        margin-right: 10px;
      }
    }
  }
  .btn-group{
    .btn{
      @include media(">=widescreen") {
        margin-right: 38px;
      }
    }
  }
  .image-holder{
    width: 100%;
    @include media(">=desktop") {
      max-width: 50%;
      padding-right: 120px;
      padding-left: 10px;
    }
    .image-block{
      line-height: 0;
      width: 100%;
      position: relative;
      &:after{
        content: '';
        position: absolute;
        z-index: 0;
        top: 40px;
        left: 50px;
        right: -40px;
        bottom: -40px;
        background: $green;
        @include media(">=desktop") {
          top: 70px;
          left: 100px;
          right: -120px;
          bottom: -90px;
        }
      }
      img{
        position: relative;
        z-index: 10;
        width: 100%;
      }
    }
  }
}