.project-top-section{
  padding-top: 40px;
}
.project-banner{
  position: relative;
  padding: 40px 0 60px;
  margin-bottom: 50px;
  @include media(">=desktop") {
    margin-bottom: 100px;
    padding: 40px 0 126px;
  }
  &:after{
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 110vw;
    background: #eeebe5;
    z-index: -5;
  }
  .banner-header{
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 980px;
    margin: 0 auto 50px;
    @include media(">=desktop") {
      margin: 0 auto 110px;
    }
    .avatar{
      width: 60px;
      position: relative;
      margin-right: 40px;
      img{
        width: 100%;
      }
      .image{
        width: 60px;
        height: 60px;
        border-radius: 50%;
        overflow: hidden;
      }
      .image-small{
        position: absolute;
        bottom: -14px;
        right: -11px;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        overflow: hidden;
        border: 4px solid #f5f4f0;
      }
    }
    h1{
      font-size: 24px;
      font-weight: 600;
      line-height: 1.1;
      margin-bottom: 10px;
    }
    .text{
      font-size: 16px;
      font-weight: 400;
      color: #000;
      a{
        text-decoration: none;
        font-weight: 600;
        &:before{
          content: '';
          display: inline-block;
          vertical-align: middle;
          margin: -2px 15px 0;
          width: 5px;
          height: 5px;
          border-radius: 50%;
          background: #000;
        }
      }
    }
  }
  .banner-image{
    text-align: center;
  }
}

.project-info{
  margin-bottom: 100px;
  padding-bottom: 30px;
  line-height: 1;
  border-bottom: 2px solid #e6e3da;
  @include media(">=widescreen") {
    padding-bottom: 54px;
    margin-bottom: 169px;
  }
  .project-date{
    font-size: 16px;
    color: #000;
    font-weight: 400;
  }
}
.project-tag-list{
  list-style-type: none;
  padding: 0;
  margin: 0 -10px 20px 0;
  display: flex;
  flex-wrap: wrap;
  max-width: 500px;
  @include media(">=desktop") {
    margin: 0 -10px 40px 0;
  }
  li{
    margin: 0 10px 15px 0;
    border: 1px solid $gray;
    background: $light-gray;
    padding: 6px 21px;
    font-size: 12px;
    color: $black-3;
    line-height: 1.1;
    font-weight: 500;
  }
}


.popup-project{
  background: #EEEBE5;
  .popup-header{
    margin-bottom: 40px;
    @include media(">=desktop") {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    @include media(">=widescreen") {
      margin-bottom: 110px;
    }
  }
  .project-list{
    display: flex;
    align-items: center;
    margin: 0 -5px;
    @include media(">=desktop") {
      margin: 0 -15px;
    }
    .project-item{
      width: 80px;
      height: 80px;
      background: $black;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      margin: 0 5px;
      @include media(">=tablet") {
        width: 120px;
        height: 120px;
      }
      @include media(">=desktop") {
        margin: 0 15px;
      }
      &.active{
        background: #f5f4f0;
      }
      img{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        max-width: 90%;
        max-height: 90%;
        width: auto;
        height: auto;
      }
    }
  }
  .popup-header-text{
    margin-bottom: 20px;
    @include media(">=tablet") {
      display: flex;
      align-items: center;
    }
    @include media(">=desktop") {
      padding-right: 30px;
      margin-bottom: 0;
    }
    .avatar{
      width: 60px;
      position: relative;
      margin-right: 40px;
      margin-bottom: 30px;
      @include media(">=tablet") {
        margin-bottom: 0;
      }
      img{
        width: 100%;
      }
      .image{
        width: 60px;
        height: 60px;
        border-radius: 50%;
        overflow: hidden;
      }
      .image-small{
        position: absolute;
        bottom: -14px;
        right: -11px;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        overflow: hidden;
        border: 4px solid #f5f4f0;
      }
    }
    h1{
      font-size: 24px;
      font-weight: 600;
      line-height: 1.1;
      margin-bottom: 10px;
    }
    .text{
      font-size: 16px;
      font-weight: 400;
      color: #000;
      a{
        text-decoration: none;
        font-weight: 600;
        &:before{
          content: '';
          display: inline-block;
          vertical-align: middle;
          margin: -2px 15px 0;
          width: 5px;
          height: 5px;
          border-radius: 50%;
          background: #000;
        }
      }
    }
  }
  .popup-project{
    width: 100%;
    background: $white;
    padding: 62px;
    text-align: center;
  }
}