.categories-section{
  margin-bottom: 50px;
  @include media(">=tablet") {
    margin-bottom: 100px;
  }
  @include media(">=widescreen") {
    margin-bottom: 140px;
  }
  .categories-text{
    margin-bottom: 50px;
    @include media(">=tablet") {
      margin-bottom: 105px;
    }
    .text-block{
      margin-bottom: 30px;
      @include media(">=widescreen") {
        margin-bottom: 55px;
      }
    }
  }

  .categories-bottom{
    font-size: 34px;
    font-weight: 600;
    color: $black;
    text-align: center;
    line-height: 1.2;
    a{
      color: $green;
      text-decoration: none;
    }
  }
}

.categories-search-form{
  position: relative;
  .form-control{
    border: 10px solid #ecebe7;
    height: 90px;
    padding: 28px 21px;
    &:focus {
      border-color: #ecebe7;
    }
  }
  .btn{
    @include media(">=tablet") {
      position: absolute;
      top: 50%;
      right: 18px;
      transform: translateY(-50%);
      min-width: 162px !important;
    }
  }
}

.categories-list-block{
  @include media(">=tablet") {
    display: flex;
    justify-content: space-between;
    margin: 0 -15px 30px;
  }
  .categories-column-block{
    @include media(">=tablet") {
      max-width: 505px;
      padding: 0 15px;
    }
  }
  .category-item-block{
    margin-bottom: 50px;
    @include media(">=tablet") {
      margin-bottom: 110px;
    }
  }
  .category-header{
    margin-bottom: 30px;
    text-align: center;
    @include media(">=tablet") {
      margin-bottom: 60px;
      text-align: left;
    }
    .category-image{
      line-height: 0;
      margin-bottom: 20px;
      @include media(">=tablet") {
        margin-bottom: 35px;
      }
    }
    .category-title{
      margin-bottom: 0;
      font-size: 34px;
      font-weight: 600;
      line-height: 1;
    }
  }
  .category-list{
    padding: 0;
    margin: 0;
    list-style-type: none;
  }
  .category-item{
    margin-bottom: 2px;
    &:last-child{
      margin-bottom: 0;
    }
    a{
      background: $light-gray;
      display: block;
      padding: 15px 20px;
      font-size: 20px;
      font-weight: 600;
      text-decoration: none;
      line-height: 1;
      color: $black;
      @include media(">=desktop") {
        padding: 22px 40px;
        font-size: 24px;
      }
      &:hover{
        background: darken($light-gray, 10%);
      }
    }
  }
}