.reviews-logo-section{
  margin-bottom: 50px;
  @include media(">=widescreen") {
    margin-bottom: 115px;
  }
  .title-global-section{
    font-weight: 400;
    max-width: 845px;
    margin: 0 auto 40px;
  }
  .reviews-logo-header{
    margin-bottom: 20px;
    @include media(">=widescreen") {
      margin-bottom: 65px;
    }
    .text-block{
      max-width: 660px;
      margin: 0 auto;
      line-height: 2.3;
    }
    p{
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  .reviews-logo-list{
    list-style-type: none;
    padding: 20px 20px 20px 0;
    margin: 0 -20px 0 0;
    display: flex;
    align-items: center;
    width: calc(100% + 20px);
    overflow-x: auto;
    overflow-y: visible;
    @include media(">=widescreen") {
      justify-content: center;
      padding: 20px;
      margin: 0;
    }
    .logo-item{
      margin-right: 30px;
      min-width: 80px;
      &:last-child{
        margin-right: 0;
        padding-right: 20px;
        @include media("<tablet") {
          min-width: 100px;
        }
        @include media("<desktop") {
          min-width: 140px;
        }
        @include media(">=desktop") {
          padding-right: 0;
        }
      }
      &.logo-item-big{
        @include media(">=tablet") {
          min-width: 140px;
        }
      }
    }
    a{
      display: block;
      line-height: 0;
      opacity: 1;
      transition: 0.3s;
      &:hover{
        opacity: 0.5;
      }
    }
  }
}