.positions-section{
  margin-bottom: 50px;
  @include media(">=widescreen") {
    margin-bottom: 120px;
  }
  .positions-list{
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: center;
    margin: 0 -10px;
    @include media(">=tablet") {
      margin: 0 -20px;
    }
  }
  .position-item{
    padding: 0 10px;
    margin-bottom: 30px;
    width: 100%;
    max-width: 50%;
    @include media(">=tablet") {
      max-width: 33.33%;
      padding: 0 20px;
    }
    @include media(">=desktop") {
      max-width: 20%;
    }
    .image-block{
      width: 100%;
      margin: 0 auto 20px;
      height: 140px;
      position: relative;
      @include media(">=tablet") {
        height: 160px;
      }
      @include media(">=desktop") {
        height: 195px;
      }
      img{
        max-width: 100%;
        width: auto;
        height: auto;
        max-height: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
    .title-block{
      font-size: 26px;
      color: $green;
      font-weight: 600;
    }
    .btn{
      @include media(">=tablet") {
        max-width: 180px;
        min-width: 180px;
      }
    }
  }
}