.info-member-section{
  margin: 0 0 100px;
  @include media(">=tablet") {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -10px 100px;
  }
  @include media(">=widescreen") {
    margin: 0 -10px 157px;
  }
  &>.title-global-section{
    width: 100%;
    margin-bottom: 50px;
    @include media(">=widescreen") {
      margin-bottom: 130px;
    }
  }
  .text-holder{
    margin-bottom: 40px;
    @include media(">=tablet") {
      margin-bottom: 0;
      max-width: 28%;
      margin-top: -13px;
      padding: 0 10px 50px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-self: stretch;
    }
    @include media(">=widescreen") {
      padding-bottom: 78px;
    }
    &.pb-0{
      padding-bottom: 0;
    }
    .title-global-section{
      margin-bottom: 20px;
      @include media(">=tablet") {
        margin-bottom: 32px;
        //height: 76px;
      }
      @include media(">=1045px") {
          //height: auto;
      }
      @include media(">=widescreen") {
        font-size: 50px;
      }
      @media (min-width: 768px) and (max-width: 1023px) {
        font-size: 32px;
      }
    }
  }
  .text-block{
    line-height: 2;
    strong{
      font-weight: 600;
    }
  }
  .image-block{
    line-height: 0;
    text-align: center;
  }
  .slider-holder{
    @include media(">=tablet") {
      max-width: 72%;
      padding: 68px 10px 0;
    }
    @include media(">=1024px") {
      padding: 77px 10px 0;
    }
    @include media(">=widescreen") {
      padding: 91px 10px 0;
    }
  }
}
.sales-list-slider, .member-list-slider, .member-work-slider {
  padding-bottom: 50px;
  @include media(">=phone") {
    margin: 0 -10px;
  }
  @include media(">=widescreen") {
    padding-bottom: 78px;
  }
  .slick-arrow {
    position: absolute;
    bottom: 0;
    &.slick-next {
      right: 0;
      @include media(">=tablet") {
        right: 10px;
      }
    }
    &.slick-prev {
      right: 97px;
      @include media(">=tablet") {
        right: 107px;
      }
    }
  }
  .member-item, .sales-item{
    padding: 0 2px;
    @include media(">=phone") {
      padding: 0 10px;
    }
  }
}
.member-list-slider{
  .member-holder{
    border: 2px solid $gray;
  }
  .member-header{
    padding: 10px 10px 18px;
    display: flex;
    align-items: flex-end;
    position: relative;
    .member-image{
      width: 100%;
      max-width: 89px;
      line-height: 0;
      @include media(">=widescreen") {
        max-width: 110px;
      }
      img{
        width: 100%;
      }
    }
    .member-text{
      padding: 47px 0 0 21px;
    }
    .name-block{
      font-size: 16px;
      font-weight: 700;
      line-height: 1.45;
      margin-bottom: 7px;
    }
    .position-block{
      font-size: 16px;
      font-weight: 400;
      margin-bottom: 0;
    }
    .level-block{
      position: absolute;
      top: -2px;
      right: -2px;
      background: $green;
      color: $white;
      font-size: 11px;
      font-weight: 400;
      line-height: 1;
      padding: 19px 25px;
    }
  }
  .member-info{
    list-style-type: none;
    padding: 0;
    margin: 0;
    font-weight: 500;
    line-height: 1.7;
    .info-item{
      border-top: 2px solid $gray;
      display: flex;
      &:last-child{
        border-bottom: 2px solid $gray;
      }
    }
    .info-title{
      width: 110px;
      flex-shrink: 0;
      border-right: 2px solid $gray;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 10px 10px;
      min-height: 72px;
      font-size: 12px;
      @include media(">=widescreen") {
        align-items: flex-start;
        font-size: 14px;
        padding: 10px 10px 10px 17px;
        min-height: 116px;
        width: 130px;
      }
    }
    .info-text{
      font-size: 14px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      flex-shrink: 0;
      padding: 10px 10px 10px 18px;
      width: calc(100% - 110px);
      line-height: 1.2;
      @include media(">=widescreen") {
        width: calc(100% - 130px);
      }
      span{
        display: block;
        margin-top: 10px;
      }
    }
    .star-block{
      font-weight: 700;
      &:before{
        content: '';
        display: inline-block;
        vertical-align: top;
        margin-right: 4px;
        margin-top: 2px;
        width: 14px;
        height: 14px;
        background-size: contain;
        background-position: 50% 50%;
        background-repeat: no-repeat;
        background-image: url("../images/icon-star.svg");
        @include media(">=widescreen") {
          margin-top: 4px;
        }
      }
    }
  }
  .member-footer{
    padding: 19px 15px 0;
    .skill-list{
      list-style-type: none;
      padding: 0;
      margin: 0 -10px 4px 0;
      display: flex;
      flex-wrap: wrap;
      li{
        margin: 0 10px 15px 0;
        border: 1px solid $gray;
        background: $light-gray;
        padding: 6px 14px;
        font-size: 12px;
        color: $black-3;
        line-height: 1.1;
        font-weight: 500;
      }
    }
    .btn{
      padding-left: 15px;
      padding-right: 15px;
      margin: 0 -18px -2px;
      width: calc(100% + 36px);
      text-align: center;
      &:before{
        position: static;
        transform: translate(0, 0);
        margin-right: 10px;
        margin-top: 6px;
      }
      &:after{
        display: none;
      }
    }
  }
}
.member-work-slider{
  .member-header{
    display: flex;
    align-items: flex-end;
    margin-bottom: 50px;
  }
  .member-image{
    border-radius: 50%;
    overflow: hidden;
    line-height: 0;
    margin-right: 18px;
    img{
      width: 100%;
    }
  }
  .member-text{
    line-height: 1.2;
    padding-bottom: 5px;
    .sub-name-block{
      font-size: 12px;
      margin-bottom: 4px;
      display: block;
    }
    .name-block{
      font-size: 16px;
      font-weight: 600;
      color: $green;
      margin-bottom: 2px;
    }
    .position-block{
      font-size: 16px;
      font-weight: 400;
      margin-bottom: 0;
    }
  }
  .member-body{
    border: 10px solid $gray-3;
    min-height: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    &>*{
      max-width: 90%;
      outline: none;
    }
    .caption-block{
      font-size: 12px;
      font-weight: 400;
      position: absolute;
      bottom: 12px;
      right: 12px;
      z-index: 10;
      color: $white;
      @include media(">=tablet") {
        color: $black;
      }
      @include media(">=widescreen") {
        bottom: 17px;
        right: 20px;
      }
      &:before{
        content: '';
        width: 5px;
        height: 5px;
        border-radius: 50%;
        background: currentColor;
        display: inline-block;
        margin: 7px 10px 0 0;
        vertical-align: top;
      }
    }
  }
}
.sales-list-slider{
  .sales-holder{
    height: 100%;
    border: 2px solid #eeebe5;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &>*{
      width: 100%;
    }
  }
  .sales-label{
    margin: -2px -2px 0 auto;
    width: 120px;
    height: 49px;
    padding: 5px;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    text-align: center;
    background: $gray;
    color: $black;
    font-weight: 600;
    &.green-label{
      background: $green;
      color: $white;
    }
    &.black-label{
      background: $black;
      color: $white;
    }
  }
  .sales-title{
    height: 150px;
    padding: 10px 15px;
    background: rgba($gray, 0.4);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 -2px 49px;
    &.bg-red{
      background: $red;
    }
    &.bg-green{
      background: $green;
    }
    .title-block, .subtitle-block{
      font-size: 16px;
      font-weight: 400;
      text-transform: uppercase;
      max-width: 100%;
      margin: 0;
    }
    .title-block{
      font-weight: 700;
      margin-bottom: 5px;
    }
  }
  .text-block{
    padding: 0 30px;
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.57;
    margin-bottom: 30px;
    p{
      margin-bottom: 12px;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  .big-text-block{
    text-align: center;
    padding: 0 10px;
    font-size: 44px;
    margin-bottom: 27px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 1;
    color: $gray;
    @include media(">=widescreen") {
      font-size: 64px;
    }
    &.text-red{
      color: $red;
    }
    &.text-green{
      color: $red;
    }
  }
  .btn{
    width: calc(100% + 4px);
    padding-left: 15px;
    padding-right: 15px;
    text-align: center;
    margin: 0 -2px;
    &:before{
      position: static;
      transform: translate(0, 0);
      margin-right: 10px;
      margin-top: 6px;
    }
    &:after{
      display: none;
    }
  }
}