.banner-free-section{
  position: relative;
  background: rgba($gray, 0.4);
  padding: 40px 10px 50px;
  margin-bottom: 100px;
  @include media(">=tablet") {
    padding: 40px 100px 50px;
  }
  @include media(">=desktop") {
    padding: 40px 200px 50px;
  }
  @include media(">=widescreen") {
    padding: 70px 250px 80px;
    margin-bottom: 149px;
  }
  .title-global-section{
    margin-bottom: 28px;

  }
  .image-block{
    display: none;
    @include media(">=tablet") {
      display: block;
      max-width: 177px;
      position: absolute;
      top: 50%;
      transform: translate(0, -50%);
    }
    @include media(">=desktop") {

    }
    @include media(">=widescreen") {
      max-width: 357px;
    }
    &.image-left{
      left: 0;
      transform: translate(-50%, -50%);
      @include media(">=desktop") {
        transform: translate(-20%, -50%);
      }
      @include media(">=widescreen") {
        transform: translate(-50%, -50%);
      }
    }
    &.image-right{
      right: 0;
      transform: translate(50%, -50%);
      @include media(">=desktop") {
        transform: translate(20%, -50%);
      }
      @include media(">=widescreen") {
        transform: translate(50%, -50%);
      }
    }
  }
  .banner-free-list{
    list-style-type: none;
    padding: 0;
    margin: 0 0 17px;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.2;
    @include media(">=phone") {
      column-count: 2;
      font-size: 14px;
    }
    @include media(">=tablet") {
      font-size: 16px;
    }
    &.only-column{
      @include media(">=phone") {
          column-count: 1;
      }
    }
    li{
      padding: 0 15px;
      margin-bottom: 10px;
      @include media(">=desktop") {
        padding: 0 30px;
      }
    }
  }

  .btn{
    padding-left: 15px;
    padding-right: 15px;
    text-align: center;
    @include media(">=tablet") {
      min-width: 232px;
    }
    &:before{
      position: static;
      transform: translate(0, 0);
      margin-right: 10px;
      margin-top: 6px;
    }
    &:after{
      display: none;
    }
  }
}