.contact-section{
  margin-bottom: 100px;
  @include media(">=widescreen") {
    margin-bottom: 165px;
  }
  .banner-holder{
    margin-bottom: 30px;
    @include media(">=widescreen") {
      margin-bottom: 50px;
    }
  }
  .text-holder{
    margin-bottom: 30px;
    @include media(">=widescreen") {
      margin-bottom: 52px;
    }
    p{
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  .title-global-section{
    line-height: 1.2;
    margin-bottom: 8px;
    font-weight: 400;
    strong{
      font-weight: 700;
    }
  }
  .text-block{
    font-size: 22px;
    color: #757471;
    font-weight: 400;
  }
}

.contact-holder{
  @include media(">=tablet") {
    display: flex;
    margin: 0 -10px;
  }
  @include media(">=widescreen") {
    margin: 0 -30px;
  }
  &.contact-holder-reverse{
    @include media(">=tablet") {
        flex-direction: row-reverse;
    }
  }
  .contact-text-block{
    margin-bottom: 20px;
    width: 100%;
    @include media(">=tablet") {
      margin-bottom: 0;
      padding: 0 10px;
      max-width: 50%;
    }
    @include media(">=desktop") {
        max-width: 500px;
    }
    @include media(">=widescreen") {
      padding: 0 30px;
    }
  }
  .contact-text-holder{
    padding: 20px 20px 30px;
    height: 100%;
    margin-bottom: 30px;
    @include media(">=widescreen") {
      padding: 45px 20px 30px;
      margin-bottom: 57px;
    }
    &:last-child{
      margin-bottom: 0;
      @include media(">=widescreen") {
        margin-bottom: 0;
      }
    }
    &.height-auto{
      height: auto;
    }
    &.custom-block{
      @include media(">=widescreen") {
        padding: 25px 23px 30px;
      }
    }
    &.bg-green{
      a, p, li, label{
        color: $white;
      }
      .btn{
        &:hover{
          background: $white;
          border-color: $white;
          color: $black;
        }
      }
    }
    .image-block{
      margin: 0 -9px 40px;
      @include media(">=widescreen") {
        margin: 0 -14px 72px;
      }
    }
    a, p, li{
      font-size: 16px;
      font-weight: 400;
      margin-bottom: 15px;
      line-height: 1.2;
      @include media(">=widescreen") {
        font-size: 22px;
        margin-bottom: 27px;
      }
      &:last-child{
        margin-bottom: 0;
      }
    }
    a{
      @extend .hover-decor-line;
      display: inline-block;
      vertical-align: top;
      text-decoration: none;
    }
    ul{
      list-style-type: none;
      padding: 0;
      margin: 0;
    }
    li{
      padding-left: 35px;
      position: relative;
      margin-bottom: 15px;
      @include media(">=widescreen") {
        margin-bottom: 19px;
      }
      &:before{
        content: '';
        position: absolute;
        top: 2px;
        left: 0;
        width: 20px;
        height: 14px;
        background-size: contain;
        background-position: 50% 50%;
        background-repeat: no-repeat;
        background-image: url("../images/icon-check.svg");
      }
    }
    .questions-image-block{
      margin-bottom: 25px;
      text-align: center;
      line-height: 0;
    }
    .questions-text{
      line-height: 1.2;
      p{
        font-weight: 600;
        margin-bottom: 20px;
      }
      p, li{
        font-size: 16px;
      }
    }
    .contact-form-holder{
      padding: 0;
      border: none;
      textarea{
        height: 105px;
        min-height: 105px;
        &.big{
          height: 340px;
          min-height: 340px;
        }
      }
    }
  }
  .contact-form-block{
    width: 100%;
    @include media(">=tablet") {
      padding: 0 10px;
      max-width: 50%;
    }
    @include media(">=desktop") {
      max-width: calc(100% - 480px);
    }
    @include media(">=widescreen") {
      padding: 0 30px;
    }
  }
  .contact-form-message{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    font-weight: 600;
    color: $white;
    position: absolute;
    text-align: center;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 10;
    padding: 20px 30px;
    @include media(">=widescreen") {
        font-size: 22px;
    }
    p, a{
      font-size: 16px;
      @include media(">=widescreen") {
        font-size: 22px;
      }
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  .contact-form-holder{
    padding: 17px 35px 40px 30px;
    border: 1px solid $gray-2;
    height: 100%;
    position: relative;
    @include media(">=widescreen") {
        border-width: 2px;
    }
    .form-column{
      margin-bottom: -14px;
      .form-row{
        margin-bottom: 14px;
      }
      .form-control{
        font-size: 12px;
        padding-left: 20px;
        padding-right: 20px;
      }
      .label-checkbox{
        padding: 0 0 0 30px;
      }
    }
    .form-row{
      margin-bottom: 29px;
      &:last-child{
        margin-bottom: 0;
      }
    }
    .form-row-file{
      @include media(">=widescreen") {
        margin-bottom: 75px;
      }
      &:last-child{
        margin-bottom: 0;
      }
      [type="file"]{
        display: none;
      }
    }
    .image-file{
      display: block;
      width: 150px;
      height: 150px;
      border-radius: 50%;
      overflow: hidden;
      background-position: 50% 50%;
      background-size: cover;
      background-repeat: no-repeat;
      background-image: url("../images/default-image.jpg");
      margin-bottom: 37px;
    }
    p{
      font-size: 16px;
      font-weight: 400;
      margin-bottom: 45px;
      &.small{
        font-size: 14px;
      }
    }
    label{
      cursor: pointer;
      color: $black;
      display: block;
      margin-bottom: 11px;
      .text-label{
        font-size: inherit;
        display: block;
        margin: 0 0 11px;
      }
    }
    .form-control{
      border-color: $gray-2;
      margin-bottom: 0;
      padding-top: 16px;
      padding-bottom: 16px;
      line-height: 1.2;
      @include media(">=widescreen") {
        border-width: 2px;
      }
    }
    textarea{
      height: 165px;
      min-height: 165px;
    }
    [type="submit"]{
      margin-top: 23px;
    }
    .label-checkbox{
      padding: 0 0 0 35px;
      font-size: 12px;
      margin: 0;
      &:before{
        border: 2px solid #E6E3DA;
        width: 15px;
        height: 15px;
        top: 0;
        left: 0;
        transform: translateY(0);
      }
      &:after{
        top: 4px;
        left: 4px;
        width: 7px;
        height: 7px;
        border: none;
        background: $green;
        transform: translateY(0) rotate(0);
      }
    }
    hr{
      margin: 43px 0 32px;
      height: 2px;
      border: none;
      background: #eeebe5;
    }
    .contact-form-footer{
      border-top: 2px solid $form-element-border-color;
      padding: 32px 0 20px;
      margin-top: 43px;
      p{
        font-weight: 600;
        margin-bottom: 20px;
      }
    }
  }
}