.profile-tabs-section{
  margin-bottom: 50px;
  @include media(">=desktop") {
    margin-bottom: 100px;
  }
  @include media(">=widescreen") {
    margin-bottom: 162px;
  }
}

.profile-tabset{
  display: flex;
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 2px solid #e8e8ec;
  @include media(">=widescreen") {
    margin-bottom: 65px;
    padding-bottom: 40px;
  }
  .profile-tabset-item{
    margin: 0 20px;
    @include media(">=widescreen") {
      margin: 0 50px;
    }
    &:first-child{
      margin-left: 0;
    }
    &:last-child{
      margin-right: 0;
    }
  }
  button{
    background: transparent;
    border: none;
    font-size: 16px;
    color: #c2b9b0;
    outline: none;
    padding: 0;
    line-height: 0;
    transition: 0.3s;
    position: relative;
    @include media(">=widescreen") {
      font-size: 20px;
    }
    &:hover, &.active{
      color: $black;
    }
    &.active{
      &:after{
        opacity: 1;
        visibility: visible;
      }
    }
    &:after{
      content: '';
      position: absolute;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background: #000;
      left: 50%;
      top: calc(100% + 33px);
      transform: translate(-50%, -50%);
      opacity: 0;
      visibility: hidden;
      transition: 0.3s;
      @include media(">=widescreen") {
        width: 15px;
        height: 15px;
        top: calc(100% + 54px);
      }
    }
  }
}

.profile-tab-content{
  .portfolio-list{
    flex-wrap: wrap;
    .portfolio-item{
      @include media("<desktop") {
        max-width: 33%;
        margin-bottom: 10px;
      }
      @include media("<tablet") {
        max-width: 50%;
      }
    }
  }
  .btn-block{
    margin-top: 40px;
    @include media(">=widescreen") {
      margin-top: 70px;
    }
  }
}

.biography-holder{
  color: #000;
  .title-global-section{
    margin-bottom: 20px;
    @include media(">=widescreen") {
      margin-bottom: 62px;
    }
    &.skill-title{
      margin-bottom: 11px;
    }
  }
  h3{
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 25px;
    max-width: 500px;
    line-height: 1.17;
    @include media(">=widescreen") {
      font-size: 34px;
    }
  }
  h5{
    font-size: 14px;
    font-weight: 400;
    line-height: 1.2;
    margin-bottom: 15px;
    @include media(">=tablet") {
      margin-bottom: 25px;
    }
  }
  .biography-text{
    margin-bottom: 30px;
    @include media(">=tablet") {
      margin-bottom: 11px;
      display: flex;
      justify-content: space-between;
    }
    .text-holder{
      width: 100%;
      max-width: 700px;
      line-height: 1.85;
      @include media(">=tablet") {
        padding-right: 20px;
      }
    }
    .sidebar-holder{
      width: 100%;
      max-width: 300px;
      @include media(">=tablet") {
        padding-left: 20px;
      }
    }
  }
  .biography-info{
    list-style-type: none;
    padding: 0 0 15px;
    margin: 0 0 18px;
    font-size: 16px;
    border-bottom: 2px solid #e8e8ec;
    @include media(">=tablet") {
      padding: 0 0 31px;
      margin: 0 0 38px;
    }
    li{
      margin: 0 0 13px;
      &:last-child{
        margin: 0;
      }
      .icon{
        display: inline-block;
        vertical-align: middle;
        width: 15px;
        margin-right: 3px;
      }
    }
  }
  .worked-list{
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    li{
      width: 30px;
      height: 30px;
      border-radius: 50%;
      overflow: hidden;
      margin-right: 10px;
    }
  }
  .skill-list{
    list-style-type: none;
    padding: 0;
    margin: 0 -10px 4px 0;
    display: flex;
    flex-wrap: wrap;
    max-width: 500px;
    li{
      margin: 0 10px 15px 0;
      border: 1px solid $gray;
      background: $light-gray;
      padding: 6px 21px;
      font-size: 12px;
      color: $black-3;
      line-height: 1.1;
      font-weight: 500;
    }
  }
}