.customer-stories-section{
  margin-bottom: 50px;
  @include media(">=tablet") {
    margin-bottom: 100px;
  }
  @include media(">=widescreen") {
    margin-bottom: 150px;
  }
  .title-global-section{
    max-width: 845px;
    margin: 0 auto 40px;
  }
  .customer-stories-header{
    margin-bottom: 40px;
    @include media(">=widescreen") {
      margin-bottom: 85px;
    }
    .text-block{
      max-width: 660px;
      margin: 0 auto;
      line-height: 2.3;
    }
  }
}

.customer-stories-list{
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 40px;
  @include media(">=tablet") {
    margin: 0 -10px 40px;
  }
  @include media(">=widescreen") {
    margin: 0 -10px 80px;
  }
  .customer-stories-item{
    margin-bottom: 20px;
    width: 100%;
    @include media(">=tablet") {
      padding: 0 10px;
      max-width: 50%;
    }
    @include media(">=widescreen") {
      max-width: 33.33%;
    }
  }
}

.customer-stories-holder{
  border: 4px solid #eeebe5;
  padding: 20px 20px 35px;
  .image-block{
    display: block;
    width: 100%;
    line-height: 0;
    padding-top: 65%;
    background-color: $light-gray;
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    margin-bottom: 25px;
    &.is-video{
      position: relative;
      .icon-play{
        width: 48px;
        height: 48px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 10;
      }
    }
  }
  .label-block{
    font-size: 10px;
    font-weight: 400;
    margin-bottom: 30px;
  }
  .title-block, .text-block{
    font-size: 16px;
    line-height: 2.3;
  }
  .title-block{
    color: $green;
    font-weight: 600;
  }
  .text-block{
    margin-bottom: 40px;
  }
  .customer-stories-link{
    font-size: 16px;
    font-weight: 600;
    line-height: 1.1875;
    text-decoration: none;
    color: $black;;
    position: relative;
    padding-left: 60px;
    &:hover{
      color: $green;
    }
    &:before{
      content: '';
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background: currentColor;
      display: inline-block;
      vertical-align: middle;
      position: absolute;
      top: 50%;
      left: 44px;
      transform: translate(0, -50%);
    }
    .icon-play{
      width: 30px;
      height: 30px;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      svg{
        width: 100%;
        height: 100%;
      }
    }
  }
}