.reviews-box-section{
  margin-bottom: 50px;
  @include media(">=widescreen") {
    margin-bottom: 125px;
  }
  .title-global-section{
    margin-bottom: 30px;
    @include media(">=tablet") {
      margin-bottom: 55px;
    }
  }
}

.reviews-box-list{
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 40px;
  @include media(">=tablet") {
    margin: 0 -10px 40px;
  }
  @include media(">=widescreen") {
    margin: 0 -10px 80px;
  }
  .reviews-box-item{
    margin-bottom: 20px;
    width: 100%;
    @include media(">=tablet") {
      padding: 0 10px;
      max-width: 50%;
    }
    @include media(">=widescreen") {
        max-width: 33.33%;
    }
  }
}

.reviews-box-holder{
  border: 4px solid #eeebe5;
  padding: 20px 18px 35px 18px;
  .reviews-box-header{
    padding: 10px 0 10px 90px;
    position: relative;
    min-height: 100px;
    margin-bottom: 25px;
    @include media(">=tablet") {
      padding: 10px 0 10px 120px;
    }
    .avatar-block{
      position: absolute;
      top: 0;
      left: 0;
      width: 80px;
      height: 80px;
      border-radius: 50%;
      overflow: hidden;
      @include media(">=tablet") {
        width: 100px;
        height: 100px;
      }
      img{
        width: 100%;
      }
    }
    .name-block{
      font-weight: 600;
      font-size: 20px;
    }
    .position-block{
      font-weight: 400;
      font-size: 16px;
      color: $black-3;
    }
    .star{
      width: 21px;
      height: 21px;
      display: inline-block;
      vertical-align: middle;
      background-size: contain;
      background-repeat: no-repeat;
      background-position: 50% 50%;
      &.star-blue{
        background-image: url("../images/star-blue.png");
      }
      &.star-red{
        background-image: url("../images/star-red.png");
      }
      &.star-green{
        background-image: url("../images/star-green.png");
      }
    }
  }
  .reviews-box-text{
    font-size: 16px;
    font-weight: 400;
    color: $black-3;
    line-height: 1.8;
    .text-block{
      margin-bottom: 30px;
    }
    .reviews-box-link{
      font-size: 16px;
      font-weight: 600;
      line-height: 1.1875;
      text-decoration: none;
      color: $green;
      &:hover{
        color: $black;
      }
      &:before{
        content: '';
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background: currentColor;
        display: inline-block;
        vertical-align: middle;
        margin-right: 8px;
        margin-top: -3px;
      }
    }
  }
}