.customer-reviews-section{
  margin-bottom: 50px;
  @include media(">=tablet") {
    margin-bottom: 100px;
  }
  @include media(">=widescreen") {
    margin-bottom: 150px;
  }
  .title-global-section{
    margin-bottom: 40px;
    @include media(">=widescreen") {
      margin-bottom: 78px;
    }
  }
  .customer-reviews-select{
    .title-global-section{
      margin-bottom: 25px;
    }
  }
}

.customer-reviews-select{
  margin-bottom: 50px;
  position: relative;
  padding: 42px 0 49px;
  @include media(">=widescreen") {
    margin-bottom: 119px;
  }
  &:after{
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 110vw;
    background: $light-gray;
    z-index: -1;
  }
  .nice-select{
    border: 10px solid #ecebe7;
    height: 90px;
    padding: 28px 21px;
    background-size: 14px;
    background-position: calc(100% - 20px) 50%;
    .current {
      font-size: 16px;
    }
  }
}

.customer-reviews-list{
  margin-bottom: 50px;
  @include media(">=tablet") {
    margin-bottom: 100px;
  }
  @include media(">=widescreen") {
    margin-bottom: 145px;
  }
}

.customer-review-item{
  margin-bottom: 50px;
  @include media(">=tablet") {
    margin-bottom: 100px;
    display: flex;
    flex-wrap: wrap;
  }
  &:last-child{
    margin-bottom: 0;
  }
  &.customer-review-item-big{
    display: block;
    @include media(">=widescreen") {
      margin-bottom: 134px;
    }
    .customer-review{
      width: 100%;
      margin-bottom: 40px;
      @include media(">=tablet") {
        max-width: none;
        padding-right: 0;
        margin-bottom: 69px;
      }
      @include media(">=widescreen") {
        max-width: none;
        padding-right: 0;
      }
    }
    .member-review{
      width: 100%;
      @include media(">=tablet") {
        max-width: none;
      }
      @include media(">=widescreen") {
        max-width: none;
      }
    }
  }
  .project-info{
    font-size: 14px;
    font-weight: 400;
    color: #000;
    line-height: 1.8;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    @include media(">=tablet") {
      margin-bottom: 45px;
      min-height: 100px;
    }
    strong{
      display: block;
      font-weight: 600;
    }
  }
  .customer-review{
    width: 100%;
    @include media(">=tablet") {
      max-width: 45%;
      padding-right: 20px;
    }
    @include media(">=widescreen") {
      max-width: 500px;
      padding-right: 40px;
    }
  }
  .customer-review-block{
    display: flex;
  }
  .customer-info{
    width: 100%;
    max-width: 110px;
    padding-right: 10px;
    @include media(">=tablet") {
      max-width: 140px;
      padding-right: 40px;
    }
    .customer-avatar{
      width: 80px;
      height: 80px;
      border-radius: 50%;
      overflow: hidden;
      margin-bottom: 25px;
      img{
        width: 100%;
      }
    }
    .review-date{
      font-size: 14px;
      color: #000;
      font-weight: 600;
      line-height: 1;
      margin-bottom: 10px;
    }
    .review-rating{
      display: flex;
      .star{
        margin-right: 6px;
      }
    }
  }
  .review-title{
    font-size: 20px;
    font-weight: 600;
    color: $black-2;
    margin-bottom: 15px;
    line-height: 1.2;
    @include media(">=desktop") {
      font-size: 34px;
      margin-bottom: 25px;
    }
  }
  .review-text, .customer-position, .member-review-text{
    font-size: 14px;
    color: $black-3;
    line-height: 1.8;
    margin-bottom: 15px;
    @include media(">=desktop") {
      margin-bottom: 25px;
      font-size: 16px;
    }
    p:last-child{
      margin-bottom: 0;
    }
  }
  .customer-name{
    font-size: 20px;
    font-weight: 600;
    color: $black;
  }
  .customer-position, .member-review-text{
    @include media(">=tablet") {
      margin-bottom: 0;
    }
    @include media(">=desktop") {
      margin-bottom: 0;
    }
  }

  .star{
    width: 14px;
    height: 14px;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: contain;
    display: inline-block;
    vertical-align: middle;
    background-image: url("../images/star.png");
  }

  .member-review{
    width: 100%;
    @include media(">=tablet") {
      max-width: 55%;
    }
    @include media(">=widescreen") {
      max-width: calc(100% - 500px);

    }
    .member-review-block{
      position: relative;
      @include media(">=tablet") {
        padding-left: 100px;
      }
      @include media(">=desktop") {
        padding-left: 200px;
      }
      @include media(">=widescreen") {
        padding-left: 240px;
      }
    }
    .member-avatar{
      width: 80px;
      height: 80px;
      border-radius: 50%;
      overflow: hidden;
      position: absolute;
      top: 0;
      left: 0;
      @include media(">=tablet") {
        margin-bottom: 0;
        top: 15px;
      }
      @include media(">=desktop") {
        width: 180px;
        height: 180px;
      }
      img{
        width: 100%;
      }
    }
    .member-info{
      margin-bottom: 20px;
      line-height: 1;
      position: relative;
      padding-left: 110px;
      @include media(">=desktop") {
        padding-left: 0;
        margin-bottom: 45px;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        position: static;
      }
    }
    .member-info-header{
      margin-bottom: 20px;
      @include media(">=desktop") {
        margin-bottom: 0;
      }
    }
  }
  .member-name{
    font-size: 34px;
    color: $black-2;
    margin-bottom: 5px;
    font-weight: 600;
  }
  .member-position{
    font-size: 16px;
  }
  .member-rating-block{
    display: flex;
    font-size: 14px;
  }
  .member-rating{
    font-weight: 700;
    .star{
      margin-right: 10px;
      margin-top: -4px;
    }
  }
  .member-rating-numbers{
    margin-left: 25px;
  }

  .member-review-all{
    .member-info{
      line-height: 1;
      padding: 27px 20px 27px 120px;
      background: $light-gray;
      border: 1px solid $white;
      box-shadow: 0 0 0 10px $light-gray;
      margin-bottom: 30px;
      position: relative;
      @include media(">=tablet") {
        padding: 62px 140px 54px 360px;
        margin-bottom: 57px;
      }
      &:after{
        content: '';
        position: absolute;
        bottom: 0;
        right: 10px;
        transform: translate(0, calc(50% + 5px));
        width: 40px;
        height: 40px;
        background-size: contain;
        background-position: 50% 50%;
        background-repeat: no-repeat;
        background-image: url("../images/decor-review.png");
        @include media(">=tablet") {
          width: 60px;
          height: 60px;
        }
      }
      .member-avatar{
        width: 80px;
        height: 80px;
        border-radius: 50%;
        overflow: hidden;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 10px;
        @include media(">=tablet") {
          width: 180px;
          height: 180px;
          left: 140px;
        }
        img{
          width: 100%;
        }
      }
      .member-position{
        margin-bottom: 15px;
      }
      .member-rating-block{
        margin-bottom: 15px;
        @include media("<phone") {
          display: block;
        }
      }
      .member-rating{
        @include media("<phone") {
          margin-bottom: 10px;
          margin-left: 0;
        }
      }
      strong{
        font-size: 14px;
        font-weight: 600;
        color: #000;
      }
    }
    .member-review-text{
      @include media(">=tablet") {
        padding: 0 140px;
      }
    }
  }


  .bottom-block{
    width: 100%;
    overflow: hidden;
    position: relative;
    line-height: 1;
    margin-top: 35px;
    a{
      font-weight: 600;
      font-size: 16px;
      color: $green;
      position: relative;
      padding-left: 26px;
      padding-right: 30px;
      display: inline-block;
      vertical-align: middle;
      text-decoration: none;
      &:hover{
        color: $black;
      }
      &:before{
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background: currentColor;
      }
      &:after{
        content: '';
        position: absolute;
        top: 50%;
        left: 100%;
        right: -9999px;
        transform: translate(0, -50%);
        height: 2px;
        background: $gray-3;
        z-index: -1;
      }
    }
  }
}

.customer-reviews-navigation{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  .number{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    background: $gray-3;
    margin: 0 5px 10px;
    text-decoration: none;
    color: #000;
    font-size: 14px;
    font-weight: 400;
    &.active{
      background: $green;
      color: $white;
    }
  }
  a{
    &:hover{
      background: $green;
      color: $white;
    }
  }
}