.logo-banner-section.custom-section{
  &+.btn-section{
    @include media(">=tablet") {
      margin-top: -80px;
    }
  }
}

.btn-section{
  padding-bottom: 58px;
  margin-bottom: 55px;
  position: relative;
  &:after{
    content: '';
    position: absolute;
    bottom: 0;
    left: -9999px;
    right: -9999px;
    height: 2px;
    background: #f5f4f0;
  }
  .text-block{
    font-size: 16px;
    font-weight: 600;
    color: $green;
    margin-bottom: 25px;
    &:last-child, p:last-child{
      margin-bottom: 0;
    }
  }
  .btn-block{
    display: inline-block;
    vertical-align: middle;
    margin-bottom: 25px;
    position: relative;
    &:before, &:after{
      content: '';
      width: 55px;
      height: 55px;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: 50% 50%;
      position: absolute;
      top: -9px;
    }
    &:before{
      background-image: url("../images/btn-block-left.png");
      left: -80px;
    }
    &:after{
      background-image: url("../images/btn-block-right.png");
      right: -80px;
    }
    .btn{
      border: 10px solid $green;
    }
  }
}